.ui-contact{margin-top:100px;flex-wrap:wrap}.ui-contact,.ui-contact__section{display:flex;justify-content:center;align-items:center}.ui-contact__section{background-color:#fff;border-radius:3px;overflow:hidden;padding:15px;width:300px;height:150px;flex-direction:column;margin:20px;box-shadow:0 0 3px 0 rgba(0,0,0,.1)}.ui-contact__section__title{font-size:12px;text-transform:uppercase;font-weight:500;letter-spacing:1px;margin-bottom:10px;color:#999}.ui-contact__section__value{font-size:14px;font-weight:500;letter-spacing:1px}