.ui-app{width:1200px;margin:0 auto}.ui-app__logo{font-size:50px;font-weight:700;text-transform:uppercase;text-align:center;padding:20px 0;letter-spacing:2px}.ui-app__nav{display:flex;justify-content:center}.ui-app__nav__item{flex:0 0 auto;padding:5px;margin:0 5px;font-size:12px;text-transform:uppercase;font-weight:500;letter-spacing:1px}.ui-app__nav__item--active{border-bottom:1px solid #666}.ui-app__routes{margin-top:30px}.ui-app__footer{margin-top:100px;border-top:1px solid #eee;display:flex;justify-content:center}.ui-app__footer__link{flex:0 0 auto;display:flex;margin:0 10px;padding:20px 10px;font-size:10px;text-transform:uppercase;font-weight:500;letter-spacing:.5px;color:#999}